<template>
     <div id="container">
          <img id="pfp" :src="'data:image/png;base64,' + pfp" />
     </div>
</template>

<script>
import axios from "axios";

export default {
     name: "Pfp",
     data() {
          return {
               pfp: this.$store.get("pfp")
          };
     },
     methods: {},
     mounted() {
          axios({
               method: "get",
               url: this.$store.get("apiUrl") + "users/fulldata",
               headers: {
                    authorization: this.$store.get("token")
               }
          })
               .then(response => {
                    if (response.data.bio != null) {
                         this.$store.set("bio", response.data.bio);
                    }
                    if (response.data.firstName != null) {
                         this.$store.set("firstName", response.data.firstName);
                    }
                    if (response.data.lastName != null) {
                         this.$store.set("lastName", response.data.lastName);
                    }
                    if (response.data.username != null) {
                         this.$store.set("username", response.data.username);
                    }
                    if (response.data.accountType != null) {
                         this.$store.set("accountType", response.data.accountType);
                    }
                    if (response.data.imageUrl != null) {
                         this.$store.set("pfp", response.data.imageUrl);
                         if (response.data.imageUrl != this.pfp) {
                              this.pfp = response.data.imageUrl;
                         }
                    }
               })
               .catch(function(error) {
                    console.log(error);
               });
     }
};
</script>

<style lang="scss" scoped>
@import "../assets/global.scss";

#container {
     position: relative;
     #pfp {
          height: 50px;
          border-radius: 50px;
          border: 2px solid color(light);
          transition: border-radius 0.2s;
          transition-timing-function: cubic-bezier(
                         0.175,
                         0.885,
                         0.32,
                         1.275
                    );
          &:hover {
               border-color: color(medium);
               border-radius: 10px;
               transition: border-radius 0.15s;
               transition-timing-function: cubic-bezier(
                         0.175,
                         0.885,
                         0.32,
                         1.275
                    );
          }
     }
}
</style>